* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

span {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(125, 184, 174);
  font-weight: 900;
  padding-bottom: 30px;
}

.TitleMain {
  background-image: linear-gradient(180deg, rgb(134, 130, 255), #b308f6, rgb(39, 96, 252));
  background-clip: text;
  color: transparent;
  font-family: 'Fredoka One', cursive;
  font-size: 50px;
  text-align: center;
  margin-top: 30px;
}

#AboutMe {
  color: white;
  font-size: 27px;
  text-align: center;
  margin-left: 100px;
  margin-right: 100px;
  margin-top: 35px;
  margin-bottom: 35px;
  font-family: 'Patrick Hand', cursive;
}
