*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

span {
  display: flex;
  align-items: center;
  justify-content: center;
  color: cadetblue;
  font-weight: 900;
}

#project {
  display: flex;
  justify-content: space-evenly;
  margin-top: 75px;
  margin-bottom: 75px;
}

#TitleMain {
  background-image: linear-gradient(180deg, rgb(134, 130, 255), #b308f6, rgb(39, 96, 252));
  background-clip: text;
  color: transparent;
  font-family: 'Fredoka One', cursive;
  font-size: 50px;
  text-align: center;
  margin-top: 30px;
}

.ProjectList {
  display: flex;
  justify-content: space-evenly;
  color: white;
  font-size: 300px;
  border-radius: 50%;
}

.NameProject {
  color: white;
  font-size: 27px;
  text-align: center;
  margin-left: 100px;
  margin-right: 100px;
  margin-top: 15px;
  margin-bottom: 35px;
  font-family: 'Patrick Hand', cursive;
}

.LinkProject {
  display:flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-decoration: none;
}
